#draftmarker {
	padding: 20px;
	background-color: #005500;
	color: #ff5555;
	font-style: italic;
	font-size: 3rem;
	border: 20px yellow dashed;

	h1 {
		font-size: 5rem;
		color: #ffffff;
	}

  @media (max-width: $width-tablet) {
    font-size: 2rem;

    h1 {
    	font-size: 2rem;
    }
  }
}


.trimlink {
	width: 100%;
	border: 1px solid black;
  background-color: orangered;
  color: white;
	box-shadow: 5px 5px black;
	margin: 0 0 1em 0;
	padding: 15px;

	a {
		background-color:white;
		box-shadow: 5px 5px black;
		padding: 5px;
		margin: 5px;
		font-family: $monospaced;
	}

	span {
		
	}
}